#csv-button {
    box-shadow: 0 0 5px black;
}

.legend {
    display: grid;
    grid-template-rows: 10% 15% 60% 10%;
    width: 200px;
    height: 35vh;
    z-index: 9999;
    position: fixed;
    bottom: 18vh;
    right: 1%;
    background-color: white;
    border: 1px solid lightgrey;
    border-radius: 5px;
    text-align: center;
    overflow: auto;
    padding: 10px;
}

.legend > div > em {
    font-size: .8em;
    white-space: wrap;
    line-height: .5em;;

    /* line-height: 0; */
}

#symbol-container {
    display: grid;
    grid-template-columns: 25% 75%;
}

#symbol-column, #symbol-labels {
    display: grid;
    grid-template-rows: auto;
}

.legend-symbol {
    height: 20px;
    width: 20px;
    margin: 5px 5px 5px 50%;
}

.legend-label {
    font-size: 1.2em;
}

#month-selector {
    z-index: 9999;
    font-size: 1.2em;
}

#month-selector > .ui.dropdown > .text {
    background-color: black;
    padding: 5px 10px;
    border-radius: 2px;
    color: white;
}

#month-selector > .ui.dropdown .menu {
    max-height: 15vh;
    overflow: auto;
    position: relative;
    /* top: 15px; */
    right: 10px;
    /* float: center; */
    z-index: 99999;
}

#month-selector > .ui.dropdown .menu > .item {
 font-size: .9em;
}

#map-data-export-button {
    position: fixed;
    bottom: 17vh;
    z-index: 999999;
    left: 5%;
}


@media only screen and  (max-width: 850px) {
    .legend {
        font-size: .7rem;
        grid-template-rows: 10% 16% 60%;
        width: 200px;
        height: 35vh;
        padding: 1em;
        position: fixed;
        bottom: 22vh;
    }

    .legend {
        font-size: .8em;
    }

    #legend-header > h3 {
        font-size: 1.3rem;
    }

    .legend-symbol {
        height: 15px;
        width: 15px;
        margin: 2px 2px 2px 70%;
    
    }

    #month-selector {
        font-size: 1.5em;
        position: relative;
        top: .5vh;
    }

    #month-selector  > select {
        padding: 3px;
        border-radius: 5px;
    }

    #legend-icon {
        position: fixed;
        bottom: 22vh;
        right: 1%;
        z-index: 999999;
    }

    #legend-close-icon {
        position: fixed;
        bottom: 55vh;
        z-index: 999999;
        right: 194px;
        background-color: black;
        height: 20px;
        width: 20px;
        border-radius: 10px;
        padding: 0px;
        /* text-align: center; */
    }

    #legend-close-icon > .icon {
        margin: 0;
        position: relative;
        left: 2px;
        /* bottom: 1px; */
    }

    #map-data-export-button {
        bottom: 30vh;
        left: 15px;
        font-size: 2.4em;
        background-color: white;
        border-radius: 5px;
        /* padding: .3em; */
    }
    #map-data-export-button > .icon {
        margin: 0;
    }
}
/* responsive container housing the chart */
.chart-responsive-container {
    margin: 2vh auto;
}

#chart-data-export-button {
    position: fixed;
    bottom: 17vh;
    z-index: 999999;
    left: 5%;
}

