/* BUTTON GROUP */
.button-group-container {
    z-index: 99999;
    text-align: center; 
    position: fixed;
    bottom: 17vh;
    width: 100%;
}

/* daily, weekly, monthly buttons */
.ui.buttons .button {
    margin: 0 2px 0 2px;
}

@media only screen and  (max-width: 850px) {
    .button-group-container {
        
        /* position: relative; */
        bottom: 22vh;
    }

    #chart-data-export-button-mobile {
        position: fixed;
