body {
    width: 100%;
    height: 100vh;
    /* min-height: 700px; */
    position: fixed;
    font-family: 'NotoSansKR-Light';
    top: 0;
    overflow: hidden;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Opera and Firefox */
}

#info-icon {
    position: fixed;
    bottom: 17vh;
    left: 10px;
    z-index: 999999;
    font-size: 1.5em;
}

/* #info .icon {
    font-size: .5em;
} */

.team-member-grid-row {
    display: grid;
    grid-template-columns: 33% auto 33%;
    grid-template-rows: auto;
}

#modal {
    position: fixed; /* Stay in place */
    z-index: 9999999; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100vh; /* Full height */
    overflow: hidden; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.6); /* Black w/ opacity */
  }

#modal-content {
    background-color: white;
    margin: 5vh auto; /* 15% from the top and centered */
    padding: 20px;
    border-radius: 10px;
    border: 1px solid #888;
    height: 90vh;
    width: 90%; /* Could be more or less, depending on screen size */
    display: grid;
    grid-template-rows: 20% 75% 5%;
    padding: 2%;
}

#modal-heading {
    width: 100%;
    text-align: center;
}

#modal-heading > h1,
#modal-heading > h2 {
    margin: 0;
    color: #4B7A90;

}

#modal-heading > h1 {
    font-size: 3em;
}

#modal-heading > h2 {
    font-size: 2.5em;
}

#welcome {
    margin: 0;
    padding: 0;
    font-size: 1.2em;
}

.about-team-member-institution {
    font-size: 1.1em;
    margin-top: .2em;
    font-weight: 600;
}

.about-team-member-department {
    font-style: italic;
}

.about-team-member {
    /* margin-bottom: 20px; */
    padding: 1em;
}
.about-team-member-role {
    font-size: 1em;
    padding-bottom: .3em;
    font-style: italic;
}

.about-team-member-name {
    font-size: 1.2em;
    font-weight: 600;
    color: #4B7A90;
}

.about-team-member-title {
    margin-top: .3em;
    font-weight: 600;
}

#modal-footer {
    text-align: right;
}

#modal-body {
    overflow: auto;
    margin-bottom: 3%;
}

.about-content-section {
    margin-bottom: 3em;
    padding: 0 2em 0 2em;
}

.about-content-section > p {
    font-size: 1.1em;
    padding: 0 2em 0 1em;
}

.about-content-section-source {
    font-size: 1.1em;
    margin-bottom: 1em;
}

.about-content-section-source > a {
    margin-left: .5em;
}

.about-content-section-heading {
    color: #4B7A90;
    font-size: 2em;
    width: 100%;
    /* text-align: center; */
}

.about-content-source-note {
    padding-left: 2em;
}

h5 {
    padding: 0;
    margin: 0;
}

#eviction-tracker {
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-rows: 20% 65% 15%;
}

#header, #footer {
    text-align: center;
    width: 100%;
    z-index: 999999;
    box-shadow: 0 0 10px lightslategrey;
    overflow: hidden;
}

#footer {
    position: fixed;
    padding: .5em;
    bottom: 0;
    background-color: white;
    height: 15vh;
}

#footer-logos {
    display: grid;
    grid-template-columns: 40% 20% 40%;
    width: 100%;
}

#left-logo {
    text-align: right;
}

#right-logo {
    text-align: left;
}

#left-logo > img {
    height: 6vh;
}

#right-logo  > img {
    height: 4vh;
}

#center-logo > img {
    height: 6vh;
}

#footer-text {
    padding: 5px 0 5px 0;
    font-size: x-small;
}


#header > h1 {
    font-size: 4em;
    margin: 1vh 0 0 0;
    /* font-family: 'Impact'; */
}

#viz-box {
    /* padding: 10px auto; */
    width: 100%;
    /* background-color: rgb(255, 255, 255); */
}

#viz-toggle {
    display: grid;
    position: fixed;
    top: 16vh;
    height: 4vh;
    /* padding-top: 1vh;  */

    width: 100%;
    grid-template-columns: 50% 50%;
    font-size: 1.5em;
    font-weight: 700;    
    /* margin-top: 1em; */
}

.viz-tab {
    background-color: lightgrey;
    height: 100%;
    line-height: 4vh;
    /* align-items: center; */
    z-index: 99998;
    opacity: .5;
    /* font-weight: 700; */
    /* font-style: b */
}

.viz-tab > h3 {
    line-height: 4vh;
    font-size: 1.3em;
}

.viz-tab:hover {
   opacity: 1;
   background-color: #4B7A90;
   ; 
}

#active-viz-tab {
    opacity: 1; 
}


/* chart dropdown, giving filter perimeters */
.ui.fluid.dropdown {
    text-align: center;
    /* width: 25%;
    margin: 0 auto; */
}

#county-dropdown-container {
    width: 100%;
    position: fixed;
    top: 10vh;
    z-index: 999999;
    padding: 0 35% 0 35%;
    font-size: large;
}

#right-logo > p {
    font-size: .9em;
    margin: 0;
    padding: 0 5% 0 5%;
}

.partner-names {
    font-weight: 700;
}


#right-logo {
    text-align: center;
}

.ui.dropdown .menu > .item {
    text-align: center;
    font-size: 1em;
    z-index: 999999;
}

.leaflet-tooltip {
    z-index: 999999;
}

.tooltip-content {
    display: grid;
    grid-template-rows: auto;
    text-align: center;
    font-size: 1.1em;
    font-family: 'Courier New', Courier, monospace;

}

.chart-tooltip-content {
    font-family: 'Courier New', Courier, monospace;
    background-color: white;
    padding: 5px;
    border-radius: 5px;
    border: 1px solid lightgrey;
    font-size: .9em;
}

.tooltip-data {
    font-size: 1.2em;
    font-weight: 600;
}

#footer-logos {
    margin: 0 0 .5em 0;
}

@media only screen and (min-width: 1100px ) {
    #modal-content {
        width: 70%;
    }
}

@media only screen and (min-width: 1300px ) {
    #modal-content {
        width: 60%;
    }
}

@media only screen and  (max-width: 850px) {

    .team-member-grid-row {
        grid-template-columns: 100%;
    }

    #info-icon {
        bottom: 22vh;
    }

    .tooltip-content {
        min-width: 200px;
        /* display: inline-block; */

    } 

    .leaflet-tooltip {
        white-space: normal;
    }
    
    .tooltip-content > div {
        word-wrap: break-word;

    }


    .chart-tooltip-content {
        font-size: 1em;
        max-width: 80%;
    }

 

    #header > h1 {
        font-size: 2em;
        line-height: 1em;
        margin-top: 3vh;
        padding: 0 5%;
    }

    #eviction-tracker {
        grid-template-rows: 25% 55% 20%;
    
    }
    #viz-toggle {
        top: 20vh;
        height: 5vh;
        font-size: 1.2em;    
    }

    .viz-tab > h3 {
        line-height: 5vh;
    }

    #left-logo > img {
        height: 5vh;
    }

    #modal-content {
        grid-template-rows: 25% 65% 10%;
    }

    
    
    #center-logo > img {
        height: 5vh;
    }

    #left-logo, #center-logo {
        width: 50%;
        float: left;
        margin-bottom: .2vh;
    }

    #left-logo {
        text-align: center;
    }
    
    #right-logo > p {
        font-size: .7em;
        margin: 0;
        padding: 0 3% 0 3%;
    }

    #footer-logos {
        display: inline-block;
        margin: 0;
        transform: scale(.8);
    }

    #county-dropdown-container {
        width: 100%;
        /* position: fixed; */
        top: 13vh;
        z-index: 999999;
        padding: 0;
        font-size: medium;
        font-size: 1.2em;
    }

    #county-dropdown-container > select {
        padding: 5px;
        border-radius: 5px;
    }

    #county-dropdown-container > select > option {
        font-size: .6em;
    }

    #footer {
        height: 20vh;
        padding: 0;
    }

    #footer > p {
        font-size: .8em;
    }

    #modal-heading {
        padding-top: 2em;
    }

    #modal-heading > h1 {
        font-size: 2em;
    }
    
    #modal-heading > h2 {
        font-size: 2em;
    }



    /* .legend {
        height: 30vh;
    } */
}
